import { useSelector } from 'react-redux';

// Якщо user увійшов (використовується тільки в компоненті боє приставка "use")
export function useAuth() {
  const { g_email } = useSelector((state) => state.user);

  return !!g_email;
}

// Перевіряє чи email під яким user зайнов належить адміністратору (використовується тільки в компоненті бо є приставка "use")
export function useIfAdmin() {
  const { g_email } = useSelector((state) => state.user);
  if (
    g_email === process.env.REACT_APP_FIREBASE_ADMIN_EMAIL_2 ||
    g_email === process.env.REACT_APP_FIREBASE_ADMIN_EMAIL
  ) {
    return true;
  } else {
    return false;
  }
}

// Перевіряє чи переданий email належить адміністратору
export function ifAdmin(g_email) {
  if (
    g_email === process.env.REACT_APP_FIREBASE_ADMIN_EMAIL_2 ||
    g_email === process.env.REACT_APP_FIREBASE_ADMIN_EMAIL
  ) {
    return true;
  } else {
    return false;
  }
}

// Перевіряє чи додане Імя і телефон (використовується тільки в компоненті бо є приставка "use")
export function useAddNameAndTel() {
  const { name, tel } = useSelector((state) => state.user);
  let ifAddNameAndTel = false;
  if (name.trim().length > 2 && tel.trim().length === 12) {
    ifAddNameAndTel = true;
  } else {
    ifAddNameAndTel = false;
  }

  return ifAddNameAndTel;
}
