import { getAuth, createUserWithEmailAndPassword } from 'firebase/auth';
import { useDispatch } from 'react-redux';
import { setUser } from '../store/slices/userSlice';

import { useNavigate } from 'react-router-dom';
import FormRegisterAndLogin from './FormRegisterAndLogin/FormRegisterAndLogin';
import { doc, setDoc, getFirestore } from 'firebase/firestore';
import { app } from '../firebase';
import { ifAdmin } from '../hooks/use-auth';

const db = getFirestore(app);

const SignUp = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  // Додає нового клієнта в BD
  const addNewClient = async (user) => {
    await setDoc(doc(db, 'clients', user.uid), {
      date: new Date().toLocaleDateString(), // Дата додавання клієнта
      date_time: new Date().toLocaleTimeString(), // Час додавання клієнта
      date_day: new Date().getDay(), // День додавання клієнта
      time_milliseconds: new Date().getTime(), // Мілісікунди з  1 січня 1970 року
      name: 'Саме новий клієнт 9', //Ім'я
      tel: '', // Тедефон
      g_fullName: '',
      g_email: user.email,
      g_localId: user.uid,
      avatar_url: '',
      groupAccompany: false, // Чи на супроводі в групі
      individualAccompany: false, // Чи індивідуальний супровід
      calculated: false, //Чи розрахований
      notes: '', //Нотатки по клієнту
      bodyVerify: new Date().getTime(),
      bodyUpdata: new Date().getTime(),
      isAdmin: ifAdmin(user.email),
    });
  };

  const handlerRegister = (event, email, password) => {
    event.preventDefault();
    const auth = getAuth();
    createUserWithEmailAndPassword(auth, email, password)
      .then((userCredential) => {
        const user = userCredential.user;
        // Додаєм нового client
        addNewClient(user);
        // Signed up
        // dispatch(
        //   setUser({
        //     g_email: user.email,
        //     g_id: user.uid,
        //   }),
        // );
        // console.log('userCredential ====  ', userCredential);
        // console.log('user ===', user);
        // navigate('/login');
      })
      .catch((error) => {
        // const errorCode = error.code;
        // const errorMessage = error.message;
        console.error(error);
      });
  };

  return <FormRegisterAndLogin title="Зареєструватися" handleClick={handlerRegister} />;
};

export default SignUp;
