import { createSlice } from '@reduxjs/toolkit';

const initialState = {
  g_email: '', // Надає google при регістрації
  name: '', //Ім'я (додає сам користувач)
  tel: '', //Телефон (додає сам користувач)
  avatar_url: '', //Ссилка на картінку аватар
  g_localId: null, // Створює gogle при першій регістрації

  date: null, // Дата додавання клієнта
  date_day: null, // День додавання клієнта
  date_time: null, // Час додавання клієнта
  time_milliseconds: null, // Мілісікунди з  1 січня 1970 року

  groupAccompany: false, // Чи на груповому супроводі
  individualAccompany: false, // Чи індивідуальний супровід
  calculated: false, //Чи розрахований

  bodyUpdata: null, //Коли останій раз були заміри
  bodyVerify: null, //Коли останій раз перевіряли заміри

  sortValue: { sort: 'name', filter: 'name', name: 'Всі по алфавіту' }, //Сортування по замовчуванню

  isAdmin: false, // Чи це адмін
};

const userSlice = createSlice({
  name: 'user',
  initialState,
  reducers: {
    setUser(state, { payload }) {
      state.g_email = payload.g_email;
      state.name = payload.name;
      state.tel = payload.tel;
      state.avatar_url = payload.avatar_url;
      state.g_localId = payload.g_localId;
      state.date = payload.date;
      state.date_day = payload.date_day;
      state.date_time = payload.date_time;
      state.time_milliseconds = payload.time_milliseconds;
      state.groupAccompany = payload.groupAccompany;
      state.individualAccompany = payload.individualAccompany;
      state.calculated = payload.calculated;
      state.bodyUpdata = payload.bodyUpdata;
      state.bodyVerify = payload.bodyVerify;
      state.sortValue = payload.sortValue;
    },
    setUserDataRedux(state, { payload }) {
      state.name = payload.name;
      state.tel = payload.tel;
    },
    isAdminTrue(state) {
      state.isAdmin = true;
    },
    setSortValue(state, { payload }) {
      state.sortValue = payload;
    },
  },
});

export const { setUser, setUserDataRedux, isAdminTrue, setSortValue } = userSlice.actions;

export default userSlice.reducer;
